.info {
	&__title {
		font-size: 20px;
		color: #000;
		font-weight: 700;

		position: relative;

		margin-top: 70px;
		margin-bottom: 35px;
		padding-top: 100px;

		&::before {
			position: absolute;
			top: 0;
			left: calc(50% - 30px);

			width: 60px;
			height: 60px;
		}

		&--location {
			&::before {
				content: url('../img/icons/location.png');
			}
		}

		&--phone {
			&::before {
				left: calc(50% - 25px);

				width: 50px;

				content: url('../img/icons/phone.png');
			}
		}

		&--media {
			&::before {
				content: url('../img/icons/social-media.png');
			}
		}
	}

	&__text {
		font-size: 14px;
		font-weight: 400;
		color: #000;

		max-width: 200px;
		margin: 0;
		margin-left: calc(50% - 100px);

		text-align: center;
	}
}
