.contact {
	position: relative;

	&__title {
		margin-bottom: 70px;
	}

	&__subtitle {
		margin-top: 100px;

		@media(min-width: 768px) {
			text-align: center;
		}
	}

	&__wrapper {
		@media(min-width: 1024px) {
			padding: 120px 80px 120px 80px;

			background: url('../img/bg/map.jpg');
			background-repeat: no-repeat;
			background-size: cover;
		}
	}

	&__list {
		display: grid;

		margin: 0;
		padding-left: 0;

		list-style: none;

		gap: 30px;
		grid-template-columns: 1fr;
		grid-template-rows: 1fr 1fr 1fr;

		@media(min-width: 1024px) {
			grid-template-columns: 1fr 1fr 1fr;
			grid-template-rows: 100%;
		}
	}

	&__item {
		height: 320px;

		text-align: center;

		border: 2px solid #e5e5e5;
		border-radius: 20px;
		background-color: #fff;
	}

	&__link {
		text-decoration: none;

		color: #184ffc;
	}

	&__text {
		font-size: 18px;
		font-weight: 400;

		margin-top: 35px;
		margin-bottom: 70px;

		color: #000;

		@media(min-width: 768px) {
			text-align: center;
		}
	}
}
