.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

.button {
  display: flex;
  margin: 0 auto;
  padding: 22px 74px;
  font-weight: 700;
  font-size: 18px;
  font-family: "Open Sans", sans-serif;
  color: #183119;
  background: #f8db3d;
  border: none;
  border-radius: 4px;

  &--map {
    cursor: pointer;
  }

  &--phone {
    cursor: pointer;
  }

  @media(min-width: 768px) {
    margin-left: calc(100% - 258px);
  }

  &:hover {
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.1);
    opacity: 0.9;
    transition: 0.5s;
  }

  &:active {
    box-shadow: 0 0 0 0 transparent;
    transition: 0.3s;
  }
}

.phone-link {
	color: #49b462;
	text-decoration: none;
}

.undecorated-link {
	text-decoration: none;
}
